<template>
    <div class="timeArt">
        <ul class="lists">
            <li class="item" v-for="(item,index) in hotart" >
                <router-link :to="{path:'/artcle/',query:{index:index,type:'timeart'}}">
                <div class="arrow_box">
                    <div class="ti"></div>
                    <div class="ci"></div>
                    <h2 class="title"><a href="/" target="_blank">{{item.title}}</a></h2>
                    <ul class="textinfo">
                        <a><img :src="item.imgurl"></a>
                        <p>{{item.des}}</p>
                    </ul>
                    <ul class="details">
                        <li class="icon-time">{{item.time-0 | formatDate}}</li>
                        <li class="zan"><span class="iconfont icon-zan"></span> {{item.zhan}}</li>
                    </ul>
                </div>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script>
    import {formatDate} from '../../common/date.js'
    export default {
        name: "time-art",
        filters:{
            formatDate(time) {
                var date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm');
            }
        },
        props:{
            hotart:{
                type:Array
            }
        }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>
.timeArt{
    .lists{
        .item{
            border-right: #111 2px solid;
            padding: 20px 0;
            .arrow_box {
                background: #222;
                box-shadow: 0px 1px 0px rgba(255,255,255,.1), inset 0px 1px 1px rgba(0,0,0,.7);
                width: 630px;
                color: #b9b9b9;
                border-radius: 6px;
                position: relative;
                p{
                    line-height: 24px;
                    padding: 0 20px 20px;
                    font-size: 13px;
                }
                .ti {
                    width: 0px;
                    height: 0px;
                    border-style: solid;
                    border-width: 0px 0 20px 22px;
                    border-color: transparent transparent transparent #111;
                    position: absolute;
                    left: 630px;
                    top: 20px;
                }
                .ci {
                    width: 10px;
                    height: 10px;
                    border-radius: 50%;
                    position: absolute;
                    left: 658px;
                    top: 16px;
                    background: #000;
                    border: 2px solid #333;
                }
                h2.title {
                    padding: 0 0 0 20px;
                    font: 16px/50px "微软雅黑", "Microsoft YaHei", Arial, Helvetica, sans-serif;
                }
                .textinfo {
                    overflow: hidden;
                    margin-top: 10px;
                    a{
                        float: left;
                        margin-right: 10px;
                        width: 158px;
                        height: 114px;
                        margin-left: 20px;
                        img{
                            width: 100%;
                            height: 100%;
                        }
                    }
                }
                .details {
                    background: rgba(1,1,1,0.3);
                    border-radius: 0 0 6px 6px;
                    padding: 0 10px;
                    margin-top: 10px;
                    li {
                        line-height: 26px;
                        display: inline;
                        font-size: 11px;
                        margin-right: 10px;
                        &.zan{
                            float: right;
                        }
                    }
                }
            }
        }
    }

}
</style>


