<template>
    <div id="home">
        <div class="header">
            <div class="binner animated bounceInLeft">
            </div>
            <div class="myCard animated bounceInRight">
                <h1>我的名片</h1>
                <ul class="lists">
                    <li>
                        姓名：未松松
                    </li>
                    <li>
                        电话：17312798793
                    </li>
                    <li>
                        微信：weisong7787467
                    </li>
                    <li>
                        邮箱：1721705261@qq.com
                    </li>
                </ul>
            </div>
        </div>

        <div class="content">
            <div class="left animated bounceInUp">
                <timeArt :hotart="home.timeart"></timeArt>
            </div>
            <div class="right animated bounceInUp">
                <div class="items">
                    <h1>
                        推荐文章
                    </h1>
                    <ul class="text-lists">
                        <li  v-for='(item,index) in home.hotart'>
                            <span class="Num">{{index.toString()[1]?index.toString():0+(index+1).toString()}}</span>
                            <router-link :to="{path:'/artcle/',query:{index:index,type:'hotart'}}" class="title" >{{item.title}}</router-link>
                        </li>
                    </ul>
                </div>
                <div class="items">
                    <h1>
                        图文并茂
                    </h1>
                    <ul class="Imgtext-lists">


                        <li  v-for='(item,index) in home.hotart' v-if="index<3">
                            <router-link :to="{path:'/artcle/',query:{index:index,type:'hotart'}}" class="title" >
                              <div class="img">
                                  <img :src="item.imgurl" alt="" >
                              </div>
                              <div class="title">
                                 {{item.title}}
                              </div>
                          </router-link>
                        </li>
                    </ul>
                </div>

            </div>
        </div>

    </div>
</template>

<script>
    import timeArt from '../timeArt/timeArt.vue'

    export default {
        name: "home",
        components:{
            timeArt
        },
        props:{
          home:{
              type:Object
          }
        },
        methods:{

        }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>
#home{
    width: 1000px;
    margin: 0 auto;
    .header{
        width: 100%;
        margin-top: 40px;
        &:after{
            content: '';
            overflow: hidden;
            display: block;
            clear: both;
        }
        .binner{
            width: 630px;
            height: 250px;
            background: url("./fengjing.jpg");
            background-size:100% 100% ;
            float: left;
        }
        .myCard{
            width: 350px;
            height: 250px;
            box-shadow: 0px 1px 0px rgba(255,255,255,.1), inset 0px 1px 2px rgba(0,0,0,.7);
            background: #2a2a2a;
            float: right;
            h1{
                font-size: 24px;
                color: #3f3e36;
                padding: 10px;
            }
            .lists{
                li{
                    font-size: 13px;
                    color: #89919a;
                    padding-left:40px ;
                    margin-top: 20px;
                    text-shadow: 0px 0px 1px #888;
                }
            }
        }
    }

    .content{
        width: 100%;
        margin-top: 40px;
        &:after{
            content: '';
            overflow: hidden;
            display: block;
            clear: both;
        }

        .left{
            float: left;
            width: 666px;
        }
        .right{
            float: right;
            width: 310px;
            .items{
                width: 100%;
                padding-left: 10px;
                padding: 20px 3px 20px 0px;
                box-shadow: 0px 1px 0px rgba(255,255,255,.1), inset 0px 1px 2px rgba(0,0,0,.7);
                background: #2a2a2a;
                margin-bottom:20px;
                h1{
                    font-size: 16px;
                    color: #fafafa;
                    margin-left: 10px;
                }
                .text-lists li{
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    color: #a6a6a6;
                    margin-top: 10px;
                    margin-left: 15px;
                    &:nth-child(1),&:nth-child(2),&:nth-child(3){
                        margin-left: 10px;
                        .Num{
                            color: #a6a6a6;
                            font-size: 19px;
                        }
                    }
                    &:nth-child(1){
                        .Num{
                            color: #f8490b;
                        }
                    }
                    .Num{
                        font-size: 15px;
                        color: #3f3e3c;
                    }
                    .title{
                        cursor: pointer;
                        font-size: 13px;
                        transition: all .5s;
                        display: inline-block;
                        color: #a6a6a6;
                        &:hover{
                            transform:translate(8px);
                        }
                    }
                }
                .Imgtext-lists{
                    li{
                        color: #a6a6a6;
                        margin-top: 16px;
                        margin-left: 10px;
                        a{
                            display: block;
                            cursor: pointer;
                            &:after{
                                content: '';
                                display: block;
                                clear: both;
                            }
                            .img{
                                float: left;
                                width: 80px;
                                height: 60px;
                                margin-right: 10px;
                                img{
                                   width: 100%;
                                    height: 100%;
                                }
                            }
                            .title{
                                color: #a6a6a6;
                                font-size: 13px;
                                line-height: 16px;
                            }
                        }
                    }
                }
            }
        }
    }

}
</style>


