<template>
  <div id="app">
    <myheader/>
    <div id="content">
      <div class="router">
      <router-view :home="home"/>
      </div>
      <myfooter ></myfooter>
    </div>
  </div>
</template>

<script>
    import myheader from './components/header/myheader.vue'
    import myfooter from './components/footer/myfooter.vue'
export default {
    name: 'App',
    components:{
        myheader,
        myfooter
    },
    data(){
        return{
            home:{}
        }

    },
    created(){
        this.$http.get('/api/home').then(res => {
            var data= res.body
            this.home=data
        })
    },
    watch:{
        '$route':'gethome'
    },
    methods:{
        gethome(){
            this.$http.get('/api/home').then(res => {
                var data= res.body
                this.home=data
            })
        }
    }

}
</script>

<style lang="less" rel='stylesheet/less'>
#content{
    width: 100%;
    background: #333;
    z-index: -1;
    .router{
        min-height: 600px;
        padding-top: 71px;
    }
}
</style>



