import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home/home.vue'
import artcle from '@/components/artcle/artcle.vue'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
        path: '/artcle',
        name: 'artcle',
        component: artcle
    },
    {
      path: '/',
        redirect:'/home'
    }
  ],
  scrollBehavior (to, from, savedPosition) { //路由返回顶部
      return { x: 0, y: 0 }
  }
})



