<template>
   <div class="animated zoomIn">
      <div v-if="home[types]" class="artcle">
         <h1 class="title">
            {{home[types][index].title}}
         </h1>
         <div class="des">
            这篇文章发布于 <span class="time">{{home[types][index].time-0 | formatDate}}</span>
         </div>
         <div class="content">
            <span v-html="home[types][index].text"></span>
         </div>
      </div>
   </div>

</template>

<script>
    import {formatDate} from '../../common/date.js';
    export default {
        name: "artcle",
        filters: {
            formatDate(time) {
                var date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm');
            }
        },
        data(){
          return{
              index:0,
              types:'hotart'
          }
        },
        props:{
          home:{
              default:{},
              type:Object
          }
        },
         created(){
            if(this.$route.query.index){
                this.index=this.$route.query.index;
                this.types=this.$route.query.type;
                console.log()
            }
         }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>
.artcle{
   width: 1000px;
   margin: 0 auto;
   color: #fafafa;
   .title{
      font-size: 28px;
      padding: 10px;
      color: white;
      text-align: center;
   }
   .des{
      font-size: 15px;
      .time{
         font-size: 13px;
         margin: 2px 0px 0px 4px;
      }
   }
   .content{
      padding: 10px 0px;
   }
}
</style>


