<template>
    <div id="header">
        <ul class="lists">
            <li>
                <router-link to="/home">主页</router-link>
            </li>
            <li>
                <router-link to="/artcle" >技术探讨</router-link>
            </li>
            <li>
                <router-link to="/ss">慢生活</router-link>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "myheader"
    }
</script>

<style lang="less" rel='stylesheet/less' scoped>
#header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    background: #222;
    border-bottom: #464647 1px solid;
    .lists{
        display: flex;
        justify-content: center;
        align-items:center;
        height: 70px;
        li{
            text-align: center;
            flex: 0 100px;
            margin-left:10px;
            a{
                display: block;
                color: #fafafa;
                cursor: pointer;
                height: 40px;
                line-height: 40px;
                font-size: 13px;
                border-radius: 6px;
                &.router-link-active{
                    background: #171717;
                    box-shadow: 0px 1px 0px rgba(255,255,255,.1), inset 0px 1px 1px rgba(0,0,0,.7);
                }
                &:hover{
                    background: rgba(0,0,0,.2);
                }
            }
        }
    }

}
</style>


